import { Component, OnInit } from '@angular/core';
import { CaseService } from 'src/app/services/case.service';
import { isNgTemplate } from '@angular/compiler';
import { NewCaseComponent } from '../components/new-case/new-case.component';
import { ModalController } from '@ionic/angular';
import { ToastController } from '@ionic/angular';
import _get from 'lodash/get';

@Component({
  selector: 'app-cases',
  templateUrl: './cases.page.html',
  styleUrls: ['./cases.page.scss'],
})
export class CasesPage implements OnInit {
  myCases: any;
  sharedCases: any;
  archivedCases: any;
  caseType: string;

  constructor(
    private caseService: CaseService,
    public modalController: ModalController,
    public toastController: ToastController
  ) { }

  ngOnInit() {
    this.fetchCases();
  }

  async presentModal() {
    const modal = await this.modalController.create({
      component: NewCaseComponent
    });

    modal.onDidDismiss().then(data => {
      if(_get(data, 'data.returnedData', undefined)) {
        this.myCases.unshift({
          name: _get(data, 'data.name.value', undefined), //name: _.get(data, data.data.name.value, undefined),
          notes: _get(data, 'data.caseNote.value', undefined),
          number: _get(data, 'data.number.value', undefined),
          caseId: _get(data, 'data.returnedData.caseId', undefined)
        });

        this.presentToast();
      }
    });

    return await modal.present();
  }

  async presentToast() {
    const toast = await this.toastController.create({
      message: `New case successfully created.`,
      duration: 3000
    });
    toast.present();
  }

  fetchCases() {
    this.caseService.fetchCases().toPromise().then(res => {
      this.myCases = [...res
        .filter(item => item.writeEnabled === true && item.status === 'OPEN')
        .sort((a, b) => (a.updatedDate < b.updatedDate) ? 1 : -1)
      ];

      this.sharedCases = [...res
        .filter(item => item.writeEnabled === false)
        .sort((a, b) => (a.updatedDate < b.updatedDate) ? 1 : -1)
      ];

      this.archivedCases = [...res
        .filter(item => item.status === '' || item.status === 'CLOSE')
        .sort((a, b) => (a.updatedDate < b.updatedDate) ? 1 : -1)
      ];
    });
  }

  caseTypeChanged(e) {
    this.caseType = e.detail.value;
  }

  searchCases(e) {
    // console.log(`searching for ${e.target.value}`);
  }
}
